html {
  text-transform: uppercase;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
}

.logo {
  justify-content: center;
  width: 40px;
  height: 40px;
  display: flex;
}

.header {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0 50px;
  display: flex;
}

.nav__link {
  color: #000;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__item {
  margin: 0 0 0 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__links {
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
